.products-home-section {
	margin-top: 88px;
	padding: 50px 0;
	background: #f7f7f7;
}
.products-home-section .title {
	font-weight: 700;
	font-size: 32px;
	line-height: 32px;
	text-align: center;
}
.bg-none {
	background: none !important;
}
.products-home-section .shop-now {
	color: #000;
	font-weight: 700;
	font-size: 24px;
	line-height: 24px;
	transition: all 0.25s ease-in-out;
	text-wrap: nowrap;
}
.products-home-section .shop-now:hover {
	color: var(--primary-color);
}
.swiper {
	padding-bottom: 50px;
}
.swiper .swiper-pagination-bullet {
	border: 1px solid #d3cdcd;
	background: #fff;
	width: 12px;
	height: 12px;
}
.swiper .swiper-pagination-bullet.swiper-pagination-bullet-active {
	width: 50px;
	background: var(--primary-color);
	border-color: var(--primary-color);
	border-radius: 15px;
}
.swiper-pagination {
	bottom: 0;
}
.products-swiper {
	margin-top: 40px;
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	gap: 24px;
}
.products-swiper .category-card {
	border: none;
	cursor: pointer;
	position: relative;
}
.products-swiper .category-card .title {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: #000;
	background: #fff;
	border-radius: 10px;
	font-style: 10px;
	font-size: 18px;
	padding: 12px 30px;
	width: 75%;
	display: none;
}
.products-swiper .category-card:hover .title {
	display: block;
}
.products-swiper .category-card:hover {
	transition: all 1s ease-in-out;
	box-shadow: none;
}
.loading-page {
	min-height: 70vh;
	padding-top: 7rem;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}
.products-swiper .category-card .category-card-image {
	/* background-color: #ededed; */
	border-radius: 10px;
	margin-bottom: 20px;
	height: 380px;
	overflow: hidden;
}
.products-swiper .category-card img {
	width: 100%;
	height: 100%;
}
.products-home-section .products {
	margin: 48px 0;
	height: 100%;
}
.products-home-section .products .product {
	min-height: 100%;
	height: 100%;
	display: flex;
	flex-direction: column;
	cursor: pointer;
	border: 1px solid #d3cdcd;
	border-radius: 20px;
	margin: 0;
	padding: 0;
	overflow: hidden;
}
.products-home-section .products .product img {
	height: 280px;
}
.products-home-section .products .product .product-title {
	color: #000;
	font-weight: 700;
	font-size: 18px;
	line-height: 32px;
	margin: 0;
}
@media (min-width: 500px) and (max-width: 800px) {
	.header-home {
		height: 45vh;
	}
	.header-home .header-text {
		justify-content: end;
	}
	.products-swiper {
		margin-top: 20px;
	}
	.products-home-section {
		margin-top: 55px;
	}
	.header-home .header-img img {
		height: 300px;
	}
	.products-home-section .products .product {
		/* padding: 25px; */
	}
	.products-home-section .products .product img {
		height: 300px;
		width: 100%;
	}
	.products-swiper .category-card .category-card-image {
		height: 200px;
	}
	.en .products-home-section .main-title {
		padding-right: 32px;
	}
	.ar .products-home-section .main-title {
		padding-left: 32px;
	}
	.products-home-section .title {
		text-align: start;
		font-size: 24px;
	}
}
@media only screen and (max-width: 500px) {
	.products-home-section {
		margin-top: 24px;
	}
	.products-swiper {
		margin-top: 16px;
		grid-template-columns: repeat(2, 1fr);
	}
	.products-home-section .title {
		font-size: 18px;
		line-height: 20px;
		text-align: start;
	}
	.products-home-section .shop-now {
		font-size: 18px;
		line-height: 20px;
		text-wrap: nowrap;
	}
	.products-swiper .category-card .category-card-image {
		height: 200px;
		margin: 0;
	}
	.products-swiper .category-card .title {
		font-size: 18px;
		margin: 0;
		text-align: center;
		padding: 12px 16px;
	}
	.en .products-home-section .main-title {
		padding-right: 20px;
	}
	.ar .products-home-section .main-title {
		padding-left: 20px;
	}
	.products-home-section .products .product {
		/* padding: 25px 16px; */
	}
	.products-home-section .products .product img {
		height: 150px;
		width: 100%;
	}
	/* .swiper-slide {
		padding-bottom: 2rem !important;
	} */
	.swiper-pagination {
		bottom: -5px;
	}
	.products-home-section .products {
		margin: 24px 0;
	}
}
