.aboutUs {
	margin-bottom: 40px;
}
.static-header {
	height: calc(100vh - 78px);
	position: relative;
	background-size: cover;
}
.static-header::before {
	content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	background: linear-gradient(
		0deg,
		rgba(32, 155, 153, 0.3),
		rgba(32, 155, 153, 0.3)
	);
}
.static-header .type-title {
	color: #fff;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	font-size: 88px;
	font-weight: 700;
	line-height: 100%;
}
.aboutUs .about-us {
	display: flex;
	margin-bottom: 3rem;
}
.aboutUs .about-us .description {
	background: #f8f8f8;
	padding: 73px 75px;
}
.aboutUs .about-us .image {
}
@media only screen and (max-width: 500px) {
	.aboutUs {
		padding: 1rem;
		margin-top: 20px;
		margin-bottom: 0;
	}
	.aboutUs .type-title span {
		font-size: 40px;
		line-height: 1;
	}
	.aboutUs .about-us {
		flex-direction: column;
	}
	.aboutUs .about-us .description {
		background: #f8f8f8;
		padding: 40px 20px;
		width: 100% !important;
	}
	.aboutUs .about-us .image {
		width: 100% !important;
		margin-top: 1rem;
	}
}
