.landing-header {
	height: 100vh;
}
.landing-header .landing-navbar {
	position: absolute;
	z-index: 9999;
	width: 100%;
	display: flex;
	justify-content: space-between;
	padding: 63px 75px;
	align-items: center;
}
.landing-header .landing-navbar .landing-links {
	display: flex;
	gap: 50px;
}
.landing-header .landing-navbar .landing-links a {
	color: #fff;
	font-size: 32px;
	font-weight: 700;
	text-decoration: none;
}
.landing-header .landing-navbar .landing-logo img {
	width: 300px;
}
.landing-header .header {
	height: calc(100vh);
}
.landing-header .header img {
	height: 100%;
	width: 100%;
}
.landing-header .header::after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	z-index: 0;
	height: 100%;
	width: 100%;
	background-color: #0000004d;
}
