.landing-header .header-title {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 999;
	width: 90%;
	text-align: center;
}
.landing-header .header-title p {
	font-size: 80px;
	color: #fff;
	font-weight: 700;
}
.landing-header .carousel-control-next,
.landing-header .carousel-control-prev {
	display: none;
}
.landing-header .shop-now {
	position: absolute;
	bottom: 70px;
	left: 50%;
	transform: translate(-50%, 0);
	background-color: var(--primary-color);
	color: #fff;
	font-size: 32px;
	padding: 18px 90px;
	border-radius: 10px;
	font-weight: 700;
	z-index: 9999;
}

@media only screen and (max-width: 900px) {
}

@media only screen and (max-width: 500px) {
	.landing-header .landing-navbar {
		padding: 24px;
	}
	.landing-header .landing-navbar .landing-logo img {
		width: 100px;
	}
	.hamburger {
		position: relative;
		width: 30px;
		height: 22px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		cursor: pointer;
		background: none;
		border: none;
		padding: 0;
		transition: transform 0.3s ease;
	}
	.hamburger:focus {
		border: none;
		box-shadow: none;
	}
	.hamburger span {
		display: block;
		height: 3px;
		width: 100%;
		background: #000;
		border-radius: 2px;
		transition: all 0.6s ease;
	}
	.landing-header .hamburger span {
		background: #fff;
	}
	.hamburger.open span:nth-child(1) {
		transform: translateY(9px) rotate(45deg);
	}
	.hamburger.open span:nth-child(2) {
		opacity: 0;
	}
	.hamburger.open span:nth-child(3) {
		transform: translateY(-10px) rotate(-45deg);
	}
	.landing-header .landing-navbar .landing-links a {
		color: #000;
		font-size: 20px;
		display: block;
	}
	.landing-header .header-title p {
		font-size: 40px;
		line-height: 1.3;
	}
	.landing-header .shop-now {
		font-size: 18px;
		padding: 16px 24px;
		bottom: 120px;
	}
	.landing-header,
	.landing-header .header {
		height: calc(70vh);
	}
	.landing-header .header-title {
		top: 45%;
	}
}
