.profile {
	margin-top: 250px;
	/* overflow: hidden; */
}
.profile .profile-details {
	border: 1px solid #e4e4e4;
}
.profile .profile-details .logout {
	background: none;
	color: #848484;
	height: 50px;
	font-weight: 500;
	/* text-align: left; */
}
.profile .profile-details .logout:hover {
	color: #000;
	background-color: #dedede;
}

.profile .welcome-title {
	font-size: 24px;
	font-weight: 500;
	text-transform: capitalize;
	margin-bottom: 16px;
}
.profile .profile-details {
	padding: 30px 23px;
	border-radius: 20px;
	color: #000;
	font-size: 18px;
	height: auto;
}
.profile .details {
	padding: 40px;
	border: 1px solid #e4e4e4;
	border-radius: 20px;
}
.profile .tab-title {
	font-size: 24px;
	margin-bottom: 16px;
	font-weight: 500;
}
.profile .profile-links {
	margin-top: 24px;
}
.profile .profile-links p.active {
	color: var(--primary-color);
}

.profile .profile-links p {
	font-weight: 700;
	font-size: 18px;
	cursor: pointer;
	display: flex;
	gap: 8px;
	align-items: center;
	margin-bottom: 32px;
}
.profile .tabs {
	display: flex;
	justify-content: space-between;
	gap: 20px;
	margin-bottom: 3rem;
}
.profile .tabs .tab {
	background-color: #dedede;
	color: #000;
	padding: 12px 16px;
	width: 100%;
	border-radius: 8px;
	text-align: center;
	cursor: pointer;
	margin: 0;
}
.profile .tabs .tab:hover {
	background-color: #8b8b8b;
	color: #fff;
}
.profile .tabs .tab.active {
	background-color: var(--primary-color);
	color: #fff;
}
.profile .profile-pic .pic {
	position: relative;
}
.profile .profile-pic img {
	width: 110px;
	height: 110px;
}

.image-placeholder {
	position: relative;
	max-width: 110px;
}
.image-placeholder .avatar-edit {
	position: absolute;
	width: 100%;
	height: 100%;
	left: 0;
	right: 0;
	top: 0;
	z-index: 1;
}
.image-placeholder .avatar-edit input {
	opacity: 0;
	width: 100%;
	height: 100%;
	cursor: pointer;
}
.profile .react-tel-input .form-control {
	width: 100%;
	height: 48px;
	padding-top: 0;
	padding-bottom: 0;
}
.profile .details label {
	font-weight: 700;
}
.image-placeholder .avatar-edit input + label {
	display: inline-block;
	width: 34px;
	height: 34px;
	margin-bottom: 0;
	border-radius: 100%;
	background: #ffffff;
	border: 1px solid transparent;
	box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
	cursor: pointer;
	font-weight: normal;
	transition: all 0.2s ease-in-out;
}
.image-placeholder .avatar-edit input + label:hover {
	background: #f1f1f1;
	border-color: #d6d6d6;
}
.image-placeholder .avatar-edit input + label:after {
	content: "\F4C9";
	/* font-family: 'FontAwesome'; */
	color: #757575;
	position: absolute;
	top: 6px;
	left: 0;
	right: 0;
	text-align: center;
	margin: auto;
}
.image-placeholder .avatar-preview {
	width: 110px;
	height: 110px;
	position: relative;
	border-radius: 30px;
}
.image-placeholder .avatar-preview > div {
	width: 100%;
	height: 100%;
	border-radius: 30px;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	overflow: hidden;
}
.image-placeholder .avatar-preview > div img {
	height: 100%;
	width: 100%;
}
.orders .order-img {
	width: 100px;
	height: 100px;
}
@media (min-width: 500px) and (max-width: 800px) {
	.profile {
		margin-top: 200px;
		min-height: 40vh;
	}
	.profile .profile-details {
		padding: 12px 8px;
	}
	.profile .details {
		padding: 20px;
	}
	.orders .order-img {
		width: 100%;
	}
	.orders .col-lg-7.col-md-6 .d-flex {
		flex-direction: column;
		gap: 8px !important;
		margin-bottom: 8px;
	}
	.profile .profile-links {
		margin: 0;
	}
	.profile .profile-links p {
		margin-bottom: 24px;
	}
}
@media only screen and (max-width: 500px) {
	.profile {
		margin-top: 120px;
	}
	.profile .profile-details {
		min-height: auto;
		padding: 16px;
		margin-bottom: 1.5rem;
	}
	.profile .profile-links {
		margin: 0;
	}
	.profile .profile-links p {
		margin-bottom: 16px;
	}
	.profile .details {
		padding: 20px;
	}
}
