.about-home-section .squer {
	height: 64px;
	width: 64px;
	background-color: var(--primary-color);
	margin-bottom: 32px;
}
.about-home-section p {
	font-weight: 700;
	font-size: 24px;
	line-height: 40px;
	text-align: start;
	margin-bottom: 41px;
	color: #7a7c7e;
	width: 75%;
}
.about-home-section .shop {
	color: #fff;
	padding: 16px 80px;
	border-radius: 10px;
	text-decoration: none;
	font-size: 24px;
	background-color: var(--primary-color);
	font-weight: 700;
}
@media (min-width: 500px) and (max-width: 800px) {
	.about-home-section p {
		width: 100%;
	}
}
@media only screen and (max-width: 500px) {
	.about-home-section .squer {
		height: 40px;
		width: 40px;
	}
	.about-home-section p {
		width: 100%;
		font-size: 18px;
		line-height: 24px;
	}
	.about-home-section .shop {
		font-size: 18px;
		padding: 12px 40px;
	}
}
