.clients {
	background-color: var(--primary-color);
	padding: 77px 35px;
	margin-top: 80px;
	margin-bottom: 64px;
}
.clients .clients-images::-webkit-scrollbar {
	display: none;
}
.clients .clients-images {
	display: flex;
	gap: 84px;
	overflow: scroll;
}
.clients img {
	height: 100px;
}
.clients p {
	font-weight: 700;
	font-size: 56px;
	line-height: 80px;
	text-align: center;
	color: #fff;
}
@media only screen and (max-width: 500px) {
	.clients {
		padding: 32px;
		margin-top: 40px;
		margin-bottom: 32px;
	}
	.clients p {
		font-size: 32px;
	}
}
