.categories-page {
	min-height: 70vh;
	margin-top: 260px;
	overflow: hidden;
}
.categories-page .products {
	margin-top: 0px;
	padding: 0;
}
.categories-page .category-card img {
	/* background-color: #ededed; */
	padding: 40px 16px;
	margin-bottom: 20px;
}
.categories-page .category-card .information .title {
	margin: 0;
	color: #000;
	font-size: 20px;
	line-height: 20px;
	font-weight: 700;
}

.categories-page .type-title {
	font-size: 32px;
	line-height: 43.39px;
	font-weight: 800 !important;
	margin-bottom: 24px !important;
	margin-top: 0 !important;
}
.type-title span {
	font-size: 18px;
	font-weight: 400;
	line-height: 20.93px;
	color: #848484;
}
@media (min-width: 500px) and (max-width: 800px) {
	.categories-page {
		margin-top: 200px;
	}
}
@media only screen and (max-width: 500px) {
	.categories-page {
		margin-top: 24px !important;
	}
	.categories-page .type-title {
		font-size: 24px;
	}
}
