.header-home {
	height: 100vh;
	width: 100vw;
	position: relative;
	background-color: var(--bg-primary-color);
	overflow: hidden;
}
.header-home .chips1 {
	position: absolute;
	left: 10px;
	top: 110px;
	opacity: 0.5;
	width: 130px;
}
.header-home .chips2 {
	position: absolute;
	bottom: -50px;
	left: 50%;
	opacity: 0.5;
	width: 130px;
}
.header-home .row {
	--bs-gutter-x: 0;
}
.header-home .header-img {
	position: relative;
}
.header-home .header-img img {
	position: absolute;
	top: 65%;
	left: 0;
	transform: translate(0, -50%) !important;
	width: 100%;
	height: 450px;
	border: 1px solid var(--primary-color);
	border-radius: 30px;
}
.header-home .header-text {
	padding: 24px;
	display: flex;
	height: 100%;
	padding-top: 60px;
	flex-direction: column;
	justify-content: center;
	align-items: start;
}
.ar .header-home .header-text {
	align-items: end;
}
.header-home .header-text img {
	opacity: 0.5;
	transform: rotate(15deg);
	margin-bottom: 16px;
}

.header-home .header-text .title {
	font-size: 28px;
	font-weight: 700;
	line-height: 32px;
	margin-bottom: 32px;
}
.header-home .header-text .description {
	font-size: 24px;
	font-weight: 400;
	line-height: 100%;
	color: #7a7c7e;
	margin-bottom: 37px;
}
.header-home .header-text a {
	background-color: var(--primary-color);
	color: #fff;
	padding: 16px 80px;
	font-size: 24px;
	font-weight: 700;
	line-height: 32px;
	text-decoration: none;
	border-radius: 10px;
	display: inline-block;
}
@media only screen and (max-width: 800px) {
}
@media only screen and (max-width: 500px) {
	.header-home {
		min-height: calc(100vh - 79px);
		height: auto !important;
	}
	.header-home .swiper-slide {
		padding-top: 40px;
		padding-bottom: 0;
	}
	.header-home .row {
		height: auto !important;
		/* gap: 10px; */
	}
	.header-home .header-img {
		height: 45vh;
		margin-top: 24px;
	}
	.header-home .header-text .title,
	.header-home .header-text .description {
		margin-bottom: 20px;
	}
	.header-home .header-img img {
		max-height: 100%;
		height: 100%;
		/* height: auto; */
		/* top: 50%;
		width: 90%;
		left: 5%; */
		position: relative;
		left: auto;
		top: auto;
		transform: none !important;
	}
	.mobile-order div:first-of-type {
		order: 2;
	}
	.mobile-order div:last-of-type {
		order: 1;
	}
	.header-home .swiper {
		padding-bottom: 0 !important;
	}
	.header-home .header-text {
		text-align: center;
		height: auto;
		align-items: center !important;
		position: relative;
		padding: 24px !important;
	}
	.header-home .header-text .potatoChips {
		position: absolute;
		right: 0;
	}
	.header-home .header-text .description {
		font-size: 18px;
	}
	.header-home .header-text a {
		font-size: 18px;
		padding: 8px 40px;
	}
}
