.leadership {
	background-color: #e1f2f3;
	padding: 77px 0;
	position: relative;
	margin-top: 80px;
}
.leadership .title {
	font-weight: 700;
	font-size: 56px;
	line-height: 80px;
	margin-bottom: 64px;
	text-align: center;
}

.leadership .button-prev,
.leadership .button-next {
	position: absolute;
	top: 62%;
	transform: translateY(-50%);
	z-index: 10;
	border: none;
	background: none;
}
.leadership .button-prev {
	left: 25px;
}
.leadership .button-next {
	right: 25px;
}
.leadership .mySwiper {
	margin: 0 7.5rem;
}
.leadership .swiper-wrapper {
	display: flex;
	align-items: end;
}
.leadership .swiper-slide.swiper-slide-next img {
	height: 500px;
}
.leadership .team {
	position: relative;
}
.leadership .team img {
	height: 400px;
	width: 100%;
	border-radius: 10px;
}
.leadership .team .details {
	position: absolute;
	bottom: 18px;
	padding: 25px 16px;
	background-color: #fff;
	border-radius: 10px;
	left: 50%;
	transform: translate(-50%, 0);
	width: 75%;
}
.leadership .team .details p {
	font-family: "Cairo";
	margin: 0;
	/* text-wrap: nowrap; */
	text-align: center;
	font-size: 24px;
	font-weight: 700;
	line-height: 26px;
}

.leadership .team .details .job {
	color: #939393;
	margin-top: 14px;
}
@media only screen and (max-width: 900px) {
	.leadership {
		padding: 30px 0;
	}
	.leadership .title {
		font-size: 32px;
		margin-bottom: 32px;
	}
	.leadership .mySwiper {
		margin: 0 2.5rem;
	}
	.leadership .button-prev,
	.leadership .button-next {
		top: 50%;
	}
	.leadership .button-prev {
		left: -4px;
	}
	.leadership .button-next {
		right: -4px;
	}
	.leadership .team img,
	.leadership .swiper-slide.swiper-slide-next img {
		height: 350px;
	}
	.leadership .team .details p {
		font-size: 20px;
	}
	.leadership .team .details .job {
		font-size: 18px;
	}
}
