.product {
	margin-top: 260px;
	padding: 0 75px;
	min-height: 80vh;
}
.product .prices p {
	margin: 0;
}
.product .prices {
	width: 50%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 12px;
	height: 35.5px;
}
.product .price {
	font-size: 24px;
	color: var(--primary-color);
	font-weight: 700;
}
.product .discount {
	font-size: 16px;
	background-color: #e1f2f3;
	color: #000;
	line-height: 19.23px;
	padding: 8px 12px;
	font-weight: 500;
	border-radius: 60px;
}
.product .brand {
	margin-bottom: 16px;
}
.product .subCategory {
	color: var(--primary-color);
	padding: 4px 8px;
	border: 1px solid var(--primary-color);
	border-radius: 4px;
	margin-bottom: 1.5rem;
	display: block;
	width: fit-content;
}
.product .description {
	margin-bottom: 22px;
}
.product .brand p,
.product .description p {
	color: #000;
	font-weight: 700;
	font-size: 18px;
	margin-bottom: 12px;
	display: block;
}
.product .brand span {
	color: #000;
}
.product .description span {
	font-size: 12px;
	font-weight: 400;
	line-height: 19.61px;
	color: #848484;
}
.product .main-img {
	min-width: 490px !important;
	background-color: #e1f2f3;
	width: 100% !important;
	min-height: 500px !important;
	max-height: 550px !important;
	height: 100% !important;
	border-radius: 20px;
	padding: 40px 24px;
}
.product .main-img img {
	border-radius: 10px;
}
.product .swiper-slide img {
	width: 90px !important;
	height: 90px !important;
}
.product .swiper-pagination {
	position: relative;
	margin-top: 1.5rem;
}
.product .product-name {
	font-size: 24px;
	font-weight: 700;
	line-height: 28.13px;
	color: #000000;
	margin-bottom: 16px;
}
.gap-12 {
	gap: 12px;
}
.product .amounts {
	background-color: #e1f2f3;
	padding: 11px;
	border-radius: 5px;
	display: flex;
	gap: 32px;
}
.product .amounts button {
	font-size: 26px;
	color: #000;
	height: 32px;
	width: 32px;
	background-color: #fff;
	border: none;
	border-radius: 8px;
	font-weight: 700;
	text-align: center;
	padding: 0;
}

.product .category {
	font-size: 18px;
	background-color: var(--primary-color);
	width: fit-content;
	color: #fff;
	border-radius: 5px;
	padding: 4px 8px;
}
.product .variant-values {
	gap: 20px 50px;
	display: grid;
	grid-template-columns: auto auto auto;
}
.product .addToCart {
	background-color: var(--primary-color);
	color: #fff;
	text-decoration: none;
	width: 100%;
	font-size: 20px;
	padding: 12px 50px;
	border: 1px solid var(--primary-color);
}
.product .addToCart:hover {
	background: none;
	color: var(--primary-color);
}
.product .buy {
	background: var(--primary-color);
	height: 56px;
	font-size: 18px;
	width: 100%;
	font-weight: 700;
	border-radius: 10px;
}
.product .disabled-buy {
	background: var(--bs-gray-200) !important;
	color: #000;
	cursor: not-allowed;
}
.product .amounts span {
	font-size: 18px;
	width: 64px;
	/* height: 33px; */
	background-color: #fff;
	border-radius: 8px;
	text-align: center;
	font-weight: 700;
	line-height: 2;
	padding: 5.5;
}
.product .old-price {
	text-decoration: line-through;
	color: #838383;
}
.similar-swiper {
	padding-bottom: 24px !important;
}
.similar-swiper .swiper-pagination-bullet {
	height: 10px;
	width: 10px;
	background: #6d6969;
}
.similar-swiper .swiper-pagination-bullet.swiper-pagination-bullet-active {
	background: var(--primary-color);
}
.similar-swiper .swiper-pagination {
	bottom: -6px !important;
}
.similar_products {
	padding: 40px 75px;
	margin-top: 30px;
}
.similar_products .shop-now {
	color: var(--primary-color);
	font-size: 24px;
	font-weight: 700;
}
.similar_products h1 {
	font-size: 32px;
	font-weight: 700;
	line-height: 32px;
	margin-bottom: 40px;
}
@media only screen and (max-width: 850px) {
	.product {
		margin-top: 200px;
		padding: 0 40px;
		min-height: 70vh;
	}
	.similar_products {
		padding: 0 40px;
	}
	.product .prices {
		width: 65%;
	}
	.product .swiper-wrapper {
		justify-content: center;
	}
	.product .product-name {
		margin-top: 40px;
	}
	.product .main-img {
		width: 300px !important;
		height: 300px !important;
		padding: 24px;
	}
}
@media only screen and (max-width: 500px) {
	.product {
		margin-top: 8rem;
		margin-bottom: 4rem;
		padding: 0 20px;
		min-height: 80vh;
	}
	.product .amounts {
		gap: 12px;
	}
	.product .amounts span {
		width: 40px;
	}
	.product .prices {
		width: 100%;
	}
	.product .price {
		font-size: 20px;
	}
	.product .discount {
		font-size: 14px;
	}
	.product .main-img {
		min-width: 330px !important;
		width: 330px !important;
		min-height: 330px !important;
		height: 330px !important;
		margin-bottom: 1rem;
		padding: 24px;
	}
	.product .swiper-slide img {
		width: 60px !important;
		height: 60px !important;
	}
	.product .col-md-5 {
		margin-bottom: 2rem;
	}
	.similar_products {
		margin: 0;
		padding: 20px;
	}
	.similar_products h1 {
		font-size: 22px;
		margin-bottom: 1.5rem;
	}
}
