.navbar {
	position: absolute;
	top: 43px;
	z-index: 2;
	background: #fff;
	box-shadow: 0px 0px 12px 0px #0000001a;
	border-radius: 50px;
	padding: 20px;
	width: 90%;
	left: 5%;
	margin: auto;
}
