.navbar .search-input input:focus,
.navbar .search-input input {
	border: none;
	outline: none;
	font-size: 18px;
	width: 100%;
	position: relative;
}
.profile-dropdown button::after {
	display: none;
}
.profile-dropdown .dropdown-item {
	font-size: 18px;
	padding: 8px 12px;
}
.profile-dropdown .dropdown-item:hover {
	background-color: var(--bg-primary-color);
}
.profile-dropdown .dropdown-item {
	text-align: start;
}
.cursor-pointer {
	cursor: pointer;
}
.navbar .search-input span {
	color: var(--bs-red);
	font-size: 24px;
	cursor: pointer;
	font-weight: 700;
	position: absolute;
	top: 50%;
	transform: translate(0, -50%);
}
.en .navbar .search-input span {
	right: 12px;
}
.ar .navbar .search-input span {
	left: 12px;
}
.navbar .search-input {
	border: 1px solid #0000001a;
	padding: 6px 12px;
	border-radius: 10px;
	width: 100%;
	background-color: #fff;
	position: relative;
}
.navbar .one {
	order: 1;
	z-index: 9999;
}
.navbar .two {
	order: 2;
}
.navbar .three {
	order: 3;
	gap: 40px;
}
.ar .navbar .navbar-nav {
	gap: 48px;
}
.en .navbar .navbar-nav {
	gap: 24px;
}

.navbar a,
.navbar .nav-link {
	color: #000;
	font-size: 24px;
	font-weight: 700;
	padding: 0;
	line-height: normal;
}
.navbar a:hover {
	color: #666;
}
.navbar a.active {
	color: var(--primary-color) !important;
}
.navbar-collapse {
	justify-content: flex-end;
}
.navbar .cart-num {
	background-color: var(--bs-danger);
	color: #fff;
	position: absolute;
	border-radius: 50%;
	right: -8px;
	width: 15px;
	height: 15px;
	top: -8px;
	margin: 0;
}
.navbar-collapse img {
	display: none;
}
.mobile {
	display: none !important;
}
.tablet {
	display: none !important;
}
.web {
	display: inline-block !important;
}
.navbar-home .dropdown-toggle span {
	margin-bottom: 0;
	text-transform: capitalize;
}
.text-wrap {
	text-wrap-mode: nowrap;
}
.navbar .mobile .search-input {
	position: relative;
}
.navbar .search-input .search-icons {
	position: absolute;
	top: 50%;
	transform: translate(-50%, 0);
	display: flex;
	gap: 8px;
}
.en .navbar .search-input .search-icons {
	right: 10px;
}
.ar .navbar .search-input .search-icons {
	left: 10px;
}
.navbar .search-input svg {
	width: 40px;
}
.navbar .login-icon-sec svg {
	width: 33px;
	height: 33px;
}
@media (min-width: 500px) and (max-width: 800px) {
	.web {
		display: none !important;
	}
	.tablet {
		display: block !important;
	}
	.topNavbar {
		padding: 18px 0 !important;
	}
	.navbar-home .dropdown-toggle span {
		margin-bottom: 0;
	}
	.navbar-home {
		padding: 0 32px;
	}
	.navbar {
		top: 30px;
		padding: 22px;
		left: 50%;
		transform: translate(-50%, 0);
	}
	/* .navbar.navbar-pages {
		padding: 22px 8px;
		width: 93%;
	} */
	.navbar .one {
		order: 1;
		z-index: 9999;
	}
	.navbar .two {
		order: 3;
		display: flex;
		justify-content: end;
	}
	.navbar .three {
		order: 2;
		z-index: 999;
		justify-content: end !important;
		gap: 24px;
	}
	.navbar-toggler {
		z-index: 999;
	}
	.navbar-collapse {
		position: fixed;
		background: #fff;
		top: 90px;
		left: 0;
		right: 0;
		z-index: 99999;
		opacity: 0;
		padding: 30px 16px;
		width: 100% !important;
		border-radius: 0 0 10px 10px;
		box-shadow: 0 8px 12px 0 #0000001a;
		/* height: 100vh;
		max-height: 100vh; */
		border-bottom: none;
		transition: all 0.25s ease-in;
	}
	.ar .navbar-collapse.show img {
		transform: rotate(180deg);
	}
	.navbar-collapse.show img {
		display: block;
	}
	.hamburger {
		position: relative;
		width: 30px;
		height: 22px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		cursor: pointer;
		background: none;
		border: none;
		padding: 0;
		transition: transform 0.3s ease;
	}
	.hamburger:focus {
		border: none;
		box-shadow: none;
	}
	.hamburger span {
		display: block;
		height: 3px;
		width: 100%;
		background: #000;
		border-radius: 2px;
		transition: all 0.6s ease;
	}
	/* Animation when menu is open */
	.hamburger.open span:nth-child(1) {
		transform: translateY(9px) rotate(45deg);
	}

	.hamburger.open span:nth-child(2) {
		opacity: 0;
	}
	.hamburger.open span:nth-child(3) {
		transform: translateY(-10px) rotate(-45deg);
	}
	.en .navbar-collapse.show {
		left: 0;
		opacity: 1;
	}
	.ar .navbar-collapse.show {
		right: 0;
		opacity: 1;
	}
	.navbar .navbar-nav {
		gap: 0;
	}
	.navbar-collapse a {
		margin-bottom: 0;
	}
}
@media only screen and (max-width: 500px) {
	.web,
	.tablet {
		display: none !important;
	}
	.mobile {
		display: block !important;
	}
	.hamburger {
		position: relative;
		width: 30px;
		height: 22px;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		cursor: pointer;
		background: none;
		border: none;
		padding: 0;
		transition: transform 0.3s ease;
	}
	.hamburger:focus {
		border: none;
		box-shadow: none;
	}
	.hamburger span {
		display: block;
		height: 3px;
		width: 100%;
		background: #000;
		border-radius: 2px;
		transition: all 0.6s ease;
	}
	/* Animation when menu is open */
	.hamburger.open span:nth-child(1) {
		transform: translateY(9px) rotate(45deg);
	}

	.hamburger.open span:nth-child(2) {
		opacity: 0;
	}

	.hamburger.open span:nth-child(3) {
		transform: translateY(-10px) rotate(-45deg);
	}
	.navbar {
		top: 0;
		position: relative;
		width: 100%;
		left: 0;
		padding: 16px;
		box-shadow: none;
	}
	/* .navbar.navbar-pages {
		width: 93%;
	} */
	.navbar-home .dropdown-toggle span {
		margin-bottom: 0;
	}
	.ar .navbar {
		direction: rtl;
	}
	.ar .navbar-collapse.show img {
		transform: rotate(180deg);
	}
	.navbar-collapse.show img {
		display: block;
	}
	.en .navbar-collapse.show {
		left: 0;
		opacity: 1;
	}
	.ar .navbar-collapse.show {
		right: 0;
		opacity: 1;
	}
	.navbar-nav .nav-link {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	.navbar .cartImg {
		height: 28px;
		width: 28px;
	}
	.profile-dropdown .mobile-profile {
		background-color: var(--primary-color);
		color: #fff;
		padding: 4px 6px;
		text-transform: capitalize;
		border-radius: 6px;
	}
	.navbar .one {
		order: 1;
		z-index: 9999;
		padding: 0;
	}
	.navbar .two {
		order: 3;
		text-align: end;
		display: flex;
		justify-content: center;
		padding: 0;
	}
	.navbar .three {
		order: 2;
		z-index: 999;
		justify-content: end !important;
		gap: 16px !important;
	}
	.navbar-toggler {
		z-index: 999;
	}
	.navbar-collapse {
		position: absolute;
		background: #fff;
		top: 70px;
		left: 0;
		right: 0;
		z-index: 99999;
		opacity: 0;
		padding: 30px 16px;
		width: 100% !important;
		border-radius: 0 0 10px 10px;
		box-shadow: 0 8px 12px 0 #0000001a;
		/* 0 3px 3px 0 #fff, 0 4px 12px 0 #0000001a, 4px 0 12px 0 #0000001a,
			-4px 0 12px 0 #0000001a; */
		/* 0px 0px 12px 0px #0000001a; */
		/* width: 75% !important; */
		/* height: 100vh;
		max-height: 100vh; */
		border-bottom: none;
		transition: all 0.25s ease-in;
	}
	/* .en .navbar .three {
		padding-right: 22px;
	}
	.ar .navbar .three {
		padding-left: 22px;
	} */
	.navbar .three .nav-link .mobile svg {
		width: 30px;
	}
	.navbar .three .nav-link {
		padding: 0;
	}
	.navbar .nav-link {
		color: #222 !important;
	}
	.navbar .navbar-nav {
		gap: 0;
	}
	.navbar-collapse a {
		margin-bottom: 0;
	}
	.navbar .logo img {
		width: 85px;
		height: 45px;
	}
}

.topNavbar {
	background-color: var(--primary-color);
	max-height: 88px;
	padding: 24px 0;
}
button.language {
	display: flex;
	align-items: center;
	justify-content: center;
	color: #fff;
	font-weight: 700;
	gap: 8px;
	font-size: 18px;
}
.topNavbar .links {
	display: flex;
	align-items: center;
	gap: 18px;
}
@media only screen and (max-width: 500px) {
	.topNavbar {
		padding: 12px 0;
	}
	.topNavbar .links {
		gap: 10px;
	}
	.topNavbar .links img {
		width: 20px;
		height: 20px;
	}
	button.language {
		font-size: 18px;
	}
	button.language img {
		height: 30px;
		width: 30px;
	}
}
