.product-card {
	height: 100%;
	cursor: pointer;
	border: 1px solid #d3cdcd;
	border-radius: 20px;
	margin: 0;
	padding: 0;
	overflow: hidden;
}
.product-card .details {
	padding: 12px 7px;
	background: #e1f2f3;
}
.old-price {
	text-decoration: line-through;
}
.product-image {
	max-height: 300px;
	height: 300px;
	width: 100%;
	margin-bottom: 20px;
}
.product-card .details h6 {
	font-weight: 700;
	font-size: 20px;
	line-height: 32px;
	cursor: pointer;
	padding-bottom: 4px;
}
.product-card .details .prices p {
	margin: 0;
}
.product-card .details .prices {
	width: 90%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 12px;
	height: 35.5px;
}
.product-card .details .price {
	font-size: 16px;
	color: var(--primary-color);
	font-weight: 500;
}
.product-card .details .discount {
	font-size: 14px;
	background-color: #fef1e6;
	color: #055902;
	line-height: 19.23px;
	padding: 8px 12px;
	font-weight: 500;
	border-radius: 60px;
}
.product-card .details .brand {
	color: #848484;
	font-weight: 500;
	font-size: 14px;
	margin-bottom: 16px;
}
.product-card .details .brand span {
	color: #000;
}
.product-card .add-to-cart-buttons {
	display: flex;
	gap: 24px;
	justify-content: space-between;
}
.product-card .add-to-cart-buttons span {
	background-color: #f1eeee;
	height: 33px;
	text-align: center;
	border-radius: 5px;
	width: 100%;
	font-size: 18px;
	color: #000;
	font-weight: 700;
	line-height: 36px;
}
.product-card .add-to-cart-buttons button:disabled {
	opacity: 0.7;
	cursor: default;
}
.product-card .add-to-cart-buttons button {
	width: 32px;
	height: 32px;
	color: #fff;
	background-color: var(--primary-color);
	border-radius: 8px;
	text-align: center;
	padding: 4px;
}
.product-card .details .add-cart {
	background-color: var(--primary-color);
	border-radius: 10px;
	padding: 0 10px;
	color: #fff;
	font-weight: 500;
	width: 100%;
	height: 32px;
	line-height: 100%;
}
@media (max-width: 500px) {
	.product-image {
		max-height: 190px;
		height: 190px;
		padding: 20px 12px;
		margin-bottom: 12px;
	}
	.product-card .details .add-cart {
		padding: 0;
	}
	/* .product-card {
		overflow: hidden;
	} */
	.product-card .details {
		height: auto;
		/* calc(100% - 150px); */
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.product-card .details .prices {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: start;
		margin-bottom: 12px;
		height: auto;
		flex-direction: column;
	}
}
