.products-page {
	margin-top: 260px;
}
.products-page .categories .title,
.products-page .products .title {
	font-weight: 700;
	font-size: 32px;
	line-height: 32px;
	margin-bottom: 40px;
}
.products-page .categories {
	min-height: auto;
	margin-top: auto;
}
.products-page .categories a {
	font-weight: 700;
	font-size: 24px;
	line-height: 24px;
	color: var(--primary-color);
}
.products-page .categories .category-card {
	cursor: pointer;
}
.products-page .categories .category-card .category-card-image {
	height: 400px;
	/* background: #ededed; */
	/* padding: 40px 16px; */
	margin-bottom: 20px;
	border-radius: 10px;
	overflow: hidden;
}
.products-page .categories .category-card img {
	border-radius: 10px;
	width: 100%;
	height: 100%;
}
.products-page .categories .category-card .title {
	font-weight: 700;
	font-size: 24px;
	line-height: 32px;
	margin: 0;
}
.products-page .products {
	margin-top: 88px;
	background-color: #f5fcfc;
	padding: 40px 0;
}
@media (min-width: 500px) and (max-width: 800px) {
	.products-page {
		margin-top: 150px;
	}
	.products-page .products {
		margin-top: 40px;
	}
	.products-page .categories .category-card .category-card-image {
		height: 300px;
	}
}
@media only screen and (max-width: 500px) {
	.products-page {
		margin-top: 80px;
	}
	.products-page .categories .title,
	.products-page .products .title {
		font-size: 24px;
		margin-bottom: 24px;
	}
	.products-page .categories .category-card .category-card-image {
		height: 250px;
	}
	.products-page .categories a {
		font-size: 18px;
	}
	.products-page .products {
		margin-top: 32px;
	}
}
