.landing-footer {
	background-color: var(--primary-color);
	color: #fff;
	padding: 48px 0 0;
	margin-top: 92px;
}
.landing-footer .logo {
	width: 250px;
	height: 100px;
}
.landing-footer .links .sec-title {
	font-weight: 700;
	font-size: 16px;
	line-height: 100%;
}
.landing-footer .links a {
	display: block;
	margin-bottom: 24px;
	color: #fff;
	font-size: 16px;
	font-weight: 400;
	line-height: 21.64px;
	text-decoration: none;
}
.landing-footer .links .address {
	margin-top: 32px;
	font-size: 12px;
	line-height: 18px;
	display: flex;
	gap: 10px;
}
.gap-8 {
	gap: 8px !important;
}
.gap-16 {
	gap: 16px !important;
}
.landing-footer .links .address span {
	display: block;
	width: fit-content;
}
.landing-footer .power {
	font-size: 14px;
	line-height: 21px;
	margin-top: 6px;
	padding-bottom: 8px;
}
.landing-footer .power a {
	font-size: 16px;
	line-height: 19.23px;
}
.footer-animation {
	height: 3px;
	background: linear-gradient(
		90deg,
		var(--bg-primary-color) 0%,
		white 50%,
		var(--bg-primary-color) 100%
	);
	width: 80%;
	margin: auto;
	margin-bottom: 0.75rem;
	animation: pulse-line 7s ease-in-out infinite;
}
@keyframes pulse-line {
	0%,
	100% {
		-webkit-transform: scalex(0.3);
		-moz-transform: scalex(0.3);
		-o-transform: scalex(0.3);
		transform: scalex(0.3);
		opacity: 0.4;
	}
	50% {
		-webkit-transform: scalex(1);
		-moz-transform: scalex(1);
		-o-transform: scalex(1);
		transform: scalex(1);
		opacity: 1;
	}
}
.mobile-view {
	display: none !important;
}
.web-view {
	display: block !important;
}
@media (min-width: 500px) and (max-width: 800px) {
	.landing-footer .logo {
		width: 150px;
		height: 75px;
	}
}
@media only screen and (max-width: 500px) {
	.mobile-view {
		display: block !important;
	}
	.web-view {
		display: none !important;
	}
	.landing-footer {
		padding: 24px 0 8px;
		margin-top: 30px;
	}
	.landing-footer .logo {
		width: 120px;
		height: 50px;
		margin: 0 !important;
	}
	.default-section .sec-title {
		margin-top: 1rem;
	}
	.default-section > div {
		text-align: center;
	}
	.default-section > div:nth-of-type(1) {
		margin-bottom: 0.75rem !important;
	}
	/* .default-section > div:nth-of-type(2) a:last-of-type {
		margin: 0;
	} */
	/* .default-section > div:nth-of-type(1) {
		order: 2;
		text-align: center;
	}
	.default-section > div:nth-of-type(2) {
		order: 1;
		text-align: center;
	}
	.default-section > div:nth-of-type(3) {
		order: 3;
		text-align: center;
	} */
	.default-section > div:nth-of-type(3) div {
		justify-content: center;
	}
	.default-section .col-lg-3:first-of-type > img {
		height: 80px;
	}
	.default-section .col-lg-3:first-of-type div {
		justify-content: center;
	}
	.landing-footer .links a {
		margin-bottom: 24px;
	}
	.landing-footer .mobile-social > div {
		gap: 16px;
	}
	.landing-footer .mobile-social img {
		width: 24px;
		height: 24px;
	}
	.landing-footer .bottom-footer {
		flex-direction: column;
	}
	.landing-footer .bottom-footer .links {
		justify-content: center;
	}
	.landing-footer .bottom-footer .links a {
		margin-bottom: 12px !important;
	}
}
