.landing-page .about-us {
	margin-top: 43px;
	text-align: center;
	padding: 0 150px;
	background-size: cover;
	max-height: 110vh;
	height: 110vh;
	position: relative;
}
.landing-page .about-us div {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 75%;
	transform: translate(-50%, -50%);
}
.landing-page .about-us::before {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	z-index: 0;
	height: 100%;
	width: 100%;
	background-color: #00000080;
}
.landing-page .about-us .title {
	font-size: 96px;
	color: #fff;
	font-weight: 700;
	margin-bottom: 20px;
	z-index: 99;
	line-height: 120px;
}
.landing-page .about-us .description {
	font-weight: 700;
	font-size: 24px;
	line-height: 40px;
	text-align: center;
	color: #fff;
	margin: 0;
}
@media only screen and (max-width: 500px) {
	.landing-page .about-us {
		padding: 24px;
		max-height: max-content;
		height: max-content;
	}
	.landing-page .about-us .title {
		font-size: 40px;
		line-height: 2;
	}
	.landing-page .about-us div {
		position: relative;
		width: auto;
		left: auto;
		top: auto;
		transform: initial;
	}
	.landing-page .about-us .description {
		font-size: 20px;
		line-height: 32px;
	}
}
