.login-wrapper {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100vh;
	max-height: 100vh;
	background-color: #fff;
	display: flex;
	z-index: 99999;
}
.en .login-wrapper .react-tel-input .form-control {
	padding-right: 3.5rem;
}
.ar .login-wrapper .react-tel-input .form-control {
	padding-left: 3.5rem;
}
.react-tel-input .flag-dropdown .selected-flag {
	padding: 0 5px;
	width: 40px;
}
.react-tel-input .country-list input {
	width: 80%;
	margin: 0 10px;
}
.ar .react-tel-input .country-list {
	left: 5px;
}
.react-tel-input .selected-flag .arrow {
	left: 10px;
}
.react-tel-input .flag-dropdown {
	left: 0;
	background: var(--bg-primary-color);
	margin: 1px;
}
.ar .react-tel-input .flag-dropdown {
	left: 0;
	padding-right: 1rem;
}
.en .react-tel-input .flag-dropdown {
	padding-right: 1rem;
}
.en .react-tel-input .selected-flag .arrow {
	left: 32px;
}
.login-wrapper .react-tel-input .form-control {
	direction: ltr !important;
	width: 100%;
}
.login-wrapper .login-aside-left {
	width: 65%;
	background-color: #e1f2f3;
	display: flex;
	align-items: center;
	justify-content: center;
}
.login-wrapper .login-aside-right {
	width: 35%;
}
.login-wrapper .dont-have-account {
	color: #7b7b7b;
	font-size: 12px;
	font-weight: 400;
	text-align: center;
	cursor: pointer;
}
.login-wrapper .dont-have-account span {
	color: var(--primary-color);
	font-weight: 700;
}

.modal-content .modal-body {
	padding: 24px;
}
.modal.show .modal-dialog {
	margin-top: 2rem;
}
.login-modal .modal-dialog {
	max-width: 431px;
}
.login-form .title,
.signup-form .title {
	text-align: center;
	font-size: 16px;
	line-height: 19.23px;
}
.login-form .form-control,
.signup-form .form-control {
	min-height: 42px;
	padding: 5px 14px;
}
.login-form .title::after,
.signup-form .title::after {
	content: "";
	width: 13rem;
	height: 2px;
	background-color: #000;
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translate(-50%, 8px);
}
.signup-form .title::after {
	width: 9rem;
}
.login-form .login-btn,
.signup-form .signup-btn {
	background-color: var(--primary-color);
	margin-top: 1rem;
	width: 100%;
	padding: 7.5px;
	border-radius: 12px;
}
.login-form .login-btn:hover,
.signup-form .signup-btn:hover {
	background-color: var(--primary-color);
	border-color: var(--primary-color);
	opacity: 0.8;
}
.signup,
.login {
	background: none;
	margin-top: 1rem;
	width: 100%;
	padding: 12px;
	color: #000;
	border: none;
}
.signup:hover,
.login:hover {
	background: none;
	color: #000;
}
.signup-btn span {
	margin-left: 1rem;
}
.forget {
	background: none;
	border: none;
	color: blue;
	padding: 0;
	margin-top: 5px;
}
.forget:hover {
	color: rgba(0, 0, 255, 0.57);
}
.showPassword {
	position: absolute;
	top: 2.3rem;
	cursor: pointer;
}
.en .showPassword {
	right: 25px;
	left: auto;
}
.ar .showPassword {
	left: 25px;
	right: auto;
}
@media (min-width: 500px) and (max-width: 800px) {
	.login-wrapper .login-aside-left {
		width: 50%;
	}
	.login-wrapper .login-aside-left img {
		width: 100%;
		padding: 24px;
	}
	.login-wrapper .login-aside-right {
		width: 50%;
	}
}
@media only screen and (max-width: 500px) {
	.login-wrapper .login-aside-left {
		display: none;
	}
	.login-wrapper .login-aside-right {
		width: 80%;
		margin: auto;
	}
	.login-wrapper .login-logo {
		width: 230px;
		height: 80px;
	}
}
